export default {
	data() {
		return {
			isShowFreeReceive:false,//是否显示免费领取玉米弹框
			receiveNum:2 //可领取玉米的次数
		}
	},
	onLoad() {

	},
	methods: {
		// 点击领取玉米-弹框
		receiveCorn(){
			this.isShowFreeReceive = true
		},
		// 领取玉米的"确定"按钮
		receiveCofirmbtn(){
			if(this.receiveNum>0){
				this.receiveNum--;
			}
			this.isShowFreeReceive = false;
		}
	}
}
